@import url("https://fonts.googleapis.com/css2?family=Noto+Sans+Thai:wght@300;400;500;600&display=swap");
* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  font-family: "Noto Sans Thai", sans-serif;
}
:root {
  --body-color: #eee;
  --sidebar-color: #fff;
  --primary-color: #a2c2f1;
  --primary-color-light: #ccc;
  --toggle-color: #000;
  --text-color: #333;
  --tran-02: all 0.2s ease;
  --tran-03: all 0.3s ease;
  --tran-04: all 0.4s ease;
  --tran-05: all 0.5s ease;
  --bg: #fff;
  --nav: #ddd;
  --mor: #eee;
  --btn: #79f;
  --btn-t: #fff;
  --cha: #000;
  --sha: rgba(0, 0, 0, 0.2);
}

.body.dark {
    --body-color: #222;
    --sidebar-color: #000;
    --primary-color: #f1e5a2;
    --primary-color-light: #444;
    --toggle-color: #fff;
    --text-color: #ddd;
    transition: var(--tran-05);
  }
.wApp {
    --display-sidebar:block;
    --display-hbk:none;
    --font-size: 22px;
    --font-size-x: 22px;

    --bor-r: 15px;
    --bor-r-s: 0 5px 0 10px;
    --bor-r-g: 10px 10px 10px 5px;
    --bor-r-t: 15px 15px 0px 0px;
    --bor-r-b: 0px 0px 15px 15px;

    --pd-t:10px;

    --goods-h: 500px;
    --goodss-h: 520px;

    --space: 10px;
    --webkit-w: 10px;
    --webkit-h: 15px;

    --body-h: 100vh;

    /* sidebar */

    --side-w: 280px;
    --side-w-minus: 310px;
    --side-p: 10px 14px;

    --side-li-h: 50px;
    --side-li-m: 10px;

    --side-li-i-w: 60px;

    --side-c-w: 88px;
    --side-c-c: 118px;
    --side-im-w: 60px;

    --side-im-t-w: 40px;

    --side-menu-h: 90px;

    --side-menu-b: 80px;

    --toggle-h: 22px;
    --toggle-w: 44px;

    --body-m-p: 20px 0 20px 20px;
    --body-c-p: 15px;
    --body-c-b: 10px;

    --h3-p: 20px;

    /* goods */

    --goods-h: 300px;
    --goods-w: 600px;

    /* vtuber */

    --vt-w: 600px;
    --vt-i-p: 0px 10px 50px 10px;
    --vt-i-c-h: 230px;
    --vt-pf-s: 256px auto;
    --vt-pf: 256px;

    --vt-t-n-h: 40px;

    /* footer */

    --f-p-i: 64px;

}
.mApp {
    --display-sidebar:none;
    --display-hbk:block;
    --font-size: 15px;
    --font-size-x: 0px;

    --bor-r: 15px;
    --bor-r-s: 0 5px 0 10px;
    --bor-r-g: 10px 10px 10px 5px;
    --bor-r-t: 15px 15px 0px 0px;
    --bor-r-b: 0px 0px 15px 15px;

    --pd-t:10px;

    --goods-h: 500px;
    --goodss-h: 520px;

    --space: 2px;
    --webkit-w: 0px;
    --webkit-h: 0px;

    --body-h: 100vh;

    /* sidebar */

    --side-w: 0px;
    --side-w-minus: 20px;
    --side-p: 10px 14px;

    --side-li-h: 50px;
    --side-li-m: 10px;

    --side-li-i-w: 60px;

    --side-c-w: 0px;
    --side-c-c: 18px;
    --side-im-w: 60px;

    --side-im-t-w: 40px;

    --side-menu-h: 90px;

    --side-menu-b: 80px;

    --toggle-h: 22px;
    --toggle-w: 44px;

    --body-m-p: 70px 0px 20px 20px;
    --body-c-p: 15px;
    --body-c-b: 10px;

    --h3-p: 20px;
    
    /* goods */
    --goods-h: 300px;
    --goods-w: 100%;

    /* vtuber */
    --vt-w: 100%;
    --vt-i-p: 0px 0px 50px 0px;
    --vt-i-c-h: auto;
    --vt-pf-s: auto;
    --vt-pf: 128px;

    --vt-t-n-h: auto;

    /* footer */

    --f-p-i: 30px;
}
::-webkit-scrollbar {
  width: var(--webkit-w);
  height: var(--webkit-h);
  background-color: var(--primary-color-light);
}
::-webkit-scrollbar-track {
  width: var(--webkit-w);
}
::-webkit-scrollbar-thumb {
  background-color: var(--primary-color);
}
::-webkit-scrollbar-thumb:hover {
  width: var(--webkit-w);
  background-color: var(--sidebar-color);
}
html {
  height: 100%;
}
.body {
  overflow-y: scroll;
  background-color: var(--body-color);
  transition: var(--tran-05);
  font-family: Arial, Helvetica, sans-serif;
  height: var(--body-h);
  font-size: var(--font-size);
}
.body.dark .menu-bar .mode i.sun {
  opacity: 1;
}
.body.dark .menu-bar .mode i.moon {
  opacity: 0;
}
.body.dark .switch::before {
  left: 24px;
}
button {
  border: 0px;
  background: rgba(0, 0, 0, 0);
}
em {
  font-style: normal;
}
form {
  color: var(--text-color);
  text-align: center;
}
header .image-text .header-text {
  display: flex;
  flex-direction: column;
}
iframe {
  border-radius: var(--bor-r);
  display: block;
}
label {
  color: var(--text-color);
  padding-top: var(--pd-t);
}
table {
  width: 100%;
}
textarea {
  font-size: var(--font-size);
}
/* Hamberger */
.btn-hbk .material-symbols-outlined {
  font-variation-settings:
  'FILL' 0,
  'wght' 600,
  'GRAD' 0,
  'opsz' 48;
  font-size: 40px;
}
.hbk {
  display: var(--display-hbk);
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  padding: var(--side-p);
  transition: var(--tran-05);
}
.btn-hbk {
  background-color: var(--body-color);
  color: var(--toggle-color);
  width: 47px;
  border-radius: 100px;
  height: 47px;
  padding: 2px;
  border: 1px white solid;
  transition: var(--tran-05);
}
.hbk-items {
  display: none;
}
.hbk-items.show {
  display: block;
}
.hbk-list {
  padding: 10px 0px 10px 30px;
}
.hbk-links {
  padding: var(--bor-r-g);
  height: var(--side-li-h);
  margin-top: var(--side-li-m);
  list-style: none;
  display: flex;
  align-items: center;
  overflow: hidden;
  background-color: var(--body-color);
  font-size: 30px;
  border-radius: 20px;
  text-decoration: none;
}
.hbk-links a {
  color:var(--toggle-color);
  text-decoration: none;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  transition: var(--tran-04);
  cursor: pointer;
  border-radius: var(--bor-r);
}
.hbk-links li .icon {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: var(--side-li-i-w);
  font-size: var(--font-size);
  text-decoration: none;
}


.hbk li.mode .toggle-switch {
  position: absolute;
  right: 0;
  top: 15px;
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: var(--side-im-w);
  text-decoration: none;
}
li {
  text-decoration: none;
}
.hbk li.mode {
  position: relative;
  border-radius: var(--bor-r);
  height: var(--side-im-w);
  text-decoration: none;
  padding: var(--bor-r-g);
  height: var(--side-li-h);
  margin-top: var(--side-li-m);
  list-style: none;
  display: flex;
  align-items: center;
  overflow: hidden;
  background-color: var(--body-color);
  color:var(--toggle-color);
  font-size: 30px;
  border-radius: 20px;
  text-decoration: none;
}


/* sidebar */
.sidebar {
  display: var(--display-sidebar);
  z-index: 2;
  position: fixed;
  top: 0;
  left: 0;
  height: 100%;
  width: var(--side-w);
  padding: var(--side-p);
  background-color: var(--sidebar-color);
  transition: var(--tran-05);
}
.sidebar header {
  position: relative;
}
.sidebar header .image-text {
  text-align: center;
  overflow: hidden;
}
.sidebar header .toggle {
  cursor: pointer;
  position: absolute;
  top: 50%;
  right: -25px;
  transform: translateY(-50%);
  height: 25px;
  width: 25px;
  background-color: var(--primary-color);
  align-items: center;
  justify-content: center;
  border-radius: 50%;
  color: var(--sidebar-color);
  font-size: 25px;
}
.sidebar li {
  height: var(--side-li-h);
  margin-top: var(--side-li-m);
  list-style: none;
  display: flex;
  align-items: center;
  overflow: hidden;
}
.sidebar li .icon {
  display: flex;
  align-items: center;
  justify-content: center;
  min-width: var(--side-li-i-w);
  font-size: var(--font-size);
}
.sidebar li .icon,
.sidebar li .text {
  color: var(--text-color);
  transition: var(--tran-02);
  cursor: pointer;
}
.sidebar li a:hover {
  background-color: var(--primary-color);
  border-radius: var(--bor-r);
}
.sidebar li a:hover .icon,
.sidebar li a:hover.text {
  color: var(--sidebar-color);
}
.sidebar li a {
  font-size: var(--font-size);
  color: var(--text-color);
  text-decoration: none;
  height: 100%;
  width: 100%;
  display: flex;
  align-items: center;
  transition: var(--tran-04);
  cursor: pointer;
  border-radius: var(--bor-r);
}
.sidebar.close {
  width: var(--side-c-w);
  background-color: var(--sidebar-color);
}
.sidebar.close .text {
  opacity: 0;
  height: 0px;
  overflow: hidden;
  white-space: nowrap;
}
.sidebar .image {
  min-width: var(--side-im-w);
  display: flex;
  align-items: center;
}
.sidebar .image-text img {
  width: var(--side-im-t-w);
  border-radius: var(--bor-r);
  overflow: hidden;
}
.sidebar .menu-bar {
  height: calc(100% - var(--side-menu-h));
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.sidebar .menu-bar .botton-content {
  bottom: var(--side-menu-b);
}
.sidebar .menu-bar .mode .toggle-switch {
  position: absolute;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100%;
  min-width: var(--side-im-w);
}
.sidebar .menu-bar .mode {
  position: relative;
  background-color: var(--primary-color-light);
  border-radius: var(--bor-r);
  height: var(--side-im-w);
}
.sidebar .menu-bar .mode .moon-sun {
  height: var(--side-im-w);
  width: var(--side-im-w);
  display: flex;
  align-items: center;
}
.sidebar .menu-bar .mode i {
  position: absolute;
}
.sidebar .menu-bar .mode i.sun {
  opacity: 0;
}
.sidebar .menu-bar .mode i.moon {
  opacity: 1;
}
.sidebar .search-box {
  background-color: var(--primary-color-light);
  border-radius: var(--bor-r);
  width: 100%;
}
.sidebar .search-box input {
  height: 100%;
  width: 100%;
  background: var(--primary-color-light);
  outline: none;
  border: none;
  border-radius: var(--bor-r);
}
.sidebar .text {
  font-size: var(--font-size-x);
  font-weight: 500;
  color: var(--text-color);
  height: auto;
  overflow: hidden;
}
.sidebar.close ~ .body-main {
  left: var(--side-c-w);
  width: calc(100% - var(--side-c-c));
  transition: var(--tran-05);
}
.sidebar.close ~ .modal {
  left: var(--side-c-w);
  width: calc(100% - var(--side-c-w));
  transition: var(--tran-05);
}
.toggle-switch .switch {
  position: relative;
  height: var(--toggle-h);
  width: var(--toggle-w);
  border-radius: var(--bor-r);
  background: var(--toggle-color);
  transition: var(--tran-05);
  cursor: pointer;
}
.toggle-switch .switch::before {
  content: "";
  top: 50%;
  left: 5px;
  border-radius: 25px;
  position: absolute;
  height: 15px;
  width: 15px;
  transform: translateY(-50%);
  background: var(--sidebar-color);
  cursor: pointer;
  transition: var(--tran-05);
  cursor: pointer;
}
.p-center {
  display: flex;
  align-items: center;
  justify-content: center;
}
/* body */
.body-main {
  position: relative;
  background-color: var(--body-color);
  left: var(--side-w);
  width: calc(100% - var(--side-w-minus));
  transition: var(--tran-05);
  padding: var(--body-m-p);
  overflow: hidden;
}
.content {
  padding: var(--body-c-p);
  border-radius: var(--body-c-b);
  background-color: var(--primary-color-light);
}
h3.h {
  padding: var(--h3-p);
  color: var(--toggle-color);
}
.goods {
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  padding: var(--bor-r-s);
  height: var(--goodss-h);
}
.goods-item {
  padding: var(--bor-r-g);
}
.good-item a {
  text-decoration: none;
}
.goods .more {
  padding: var(--pd-t);
}
.goods .more .card {
  cursor: pointer;
  height: calc(var(--goods-h) - 40px);
  width: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: var(--mor);
  border-radius: var(--bor-r);
}
.goods .more span.material-symbols-sharp {
  background-color: var(--nav);
  display: flex;
  justify-content: center;
  align-items: center;
  height: 64px;
  width: 64px;
  border-radius: 100%;
}
.goods img {
  height: calc(var(--goods-h) - 250px);
  width: 350px;
  border-radius: var(--bor-r-t);
  background-color: var(--nav);
}
.goods p.desc {
  width: calc(var(--goods-h) - 150px);
  height: 100px;
  overflow: hidden;
  padding: 0 10px;
  background-color: var(--nav);
}
.goods p.price {
  padding: 10px;
  text-align: right;
  background-color: var(--nav);
  border-radius: var(--bor-r-b);
}
.goods .space {
  overflow-x: hidden;
  height: var(--space);
}
.goods a {
  display: flex;
  justify-content: center;
  width: 100%;
  text-decoration: none;
}
.goods a.button .material-symbols-sharp {
  text-align: center;
  width: 80%;
  background-color: var(--btn);
  color: var(--btn-t);
  border-radius: var(--bor-r);
  font-size: 30px;
  padding: 10px;
  transition: all 0.5s ease-in-out;
}
.goods a.button .material-symbols-sharp:hover {
  scale: 1.2;
}
.g-center {
  display: flex;
  justify-content: center;
  align-items: center;
}
.vtuber {
  display: grid;
  gap: 15px;
  width: var(--vt-w);
  grid-template-columns: var(--vt-w);
}
.vtuber-item {
  padding: var(--vt-i-p);
}
.vtuber-item .vtuber-card {
  height: var(--vt-i-c-h);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: var(--bor-r);
  display: grid;
  grid-template-columns: var(--vt-pf-s);
  padding: 10px;
}
.vtuber-card .profile {
  display: flex;
  justify-content: center;
  align-items: center;
}
.profile .logo {
  height: calc(var(--vt-pf) / 2);
  width: calc(var(--vt-pf) / 2);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  border-radius: 100%;
}
@media only screen and (min-width: 1600px) {
  .vtuber {
    width: 600px;
    grid-template-columns: repeat(1, 600px);
  }
}
@media only screen and (min-width: 1880px) {
  .vtuber {
    width: 1200px;
    grid-template-columns: repeat(2, 600px);
  }
}
@media only screen and (min-width: 2680px) {
  .vtuber {
    width: 1800px;
    grid-template-columns: repeat(3, 600px);
  }
}
@media only screen and (min-width: 3080px) {
  .vtuber {
    width: 2400px;
    grid-template-columns: repeat(4, 600px);
  }
}
@media only screen and (min-width: 3680px) {
  .vtuber {
    width: 3000px;
    grid-template-columns: repeat(5, 600px);
  }
}
.vtuber-card .text {
  padding: 20px 10px;
}
.text .name {
  cursor: default;
  background-color: var(--mor);
  text-align: center;
  border-radius: var(--bor-r);
  max-width: 400px;
  height: var(--vt-t-n-h);
  padding: 5px;
}
.text .space {
  height: var(--space);
}
.text .link {
  cursor: default;
  background-color: var(--mor);
  text-align: center;
  border-radius: var(--bor-r);
  max-width: 400px;
  height: var(--vt-t-n-h);
}
.link a {
  text-decoration: none;
}
.text .tags {
  cursor: default;
  background-color: var(--mor);
  text-align: center;
  border-radius: var(--bor-r);
  padding: 3px;
  max-width: 400px;
  height: var(--vt-t-n-h);
}
.t {
  position: relative;
  display: inline-block;
  padding-top: 5px;
  padding-right: 5px;
  transition: all 0.5s ease-in-out;
}
.t img {
  height: 16px;
}
.t:hover {
  scale: 1.5;
}
.t .tt {
  visibility: hidden;
  width: 120px;
  background-color: var(--cha);
  color: #fff;
  text-align: center;
  border-radius: var(--bor-r);
  padding: 5px 0;

  position: absolute;
  z-index: 1;
  top: -40px;
}
.t:hover .tt {
  visibility: visible;
}
.text button.rm {
  transition: all 0.5s ease-in-out;
  cursor: pointer;
  background-color: var(--mor);
  text-align: center;
  border-radius: var(--bor-r);
  width: 100%;
  font-size: 18px;
  border: 0;
  max-width: 400px;
  height: 40px;
}
.text button.rm:hover {
  scale: 1.05;
}
.modal {
  display: none;
  position: fixed;
  justify-content: center;
  align-items: center;
  z-index: 3;
  left: 0;
  top: 0;
  width: 100%;
  height: 100%;
  overflow: auto;
  background-color: var(--cha);
  background-color: var(--sha);
  -webkit-animation-name: fadeIn;
  -webkit-animation-duration: 0.4s;
  animation-name: fadeIn;
  animation-duration: 0.4s;
}
.modal-content {
  position: absolute;
  top: 40px;
  background-color: var(--bg);
  width: 80%;
  -webkit-animation-name: slideIn;
  -webkit-animation-duration: 0.4s;
  animation-name: slideIn;
  animation-duration: 0.4s;
  border-radius: var(--bor-r);
}
.modal-content .close {
  color: var(--bg);
  float: right;
  font-size: 28px;
  font-weight: bold;
  transition: all 0.5s ease-in-out;
}
.modal-content .close:hover,
.modal-content .close:focus {
  color: var(--cha);
  text-decoration: none;
  cursor: pointer;
}
.modal-header {
  padding: 2px 16px;
  background-color: var(--btn);
  color: var(--bg);
  border-radius: 15px 15px 0 0;
}
.modal-body {
  padding: 2px 16px;
}
.modal-footer {
  padding: 2px 16px;
  background-color: var(--btn);
  color: var(--bg);
  border-radius: 0 0 15px 15px;
}
@-webkit-keyframes slideIn {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 40px;
    opacity: 1;
  }
}
@keyframes slideIn {
  from {
    top: -300px;
    opacity: 0;
  }
  to {
    top: 40px;
    opacity: 1;
  }
}
@-webkit-keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
@keyframes fadeIn {
  from {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.footer {
  display: grid;
  grid-template-columns: repeat(2, calc(50% - 5px));
  padding: 10px;
  gap: 10px;
}
.support {
  padding: 0px 10px;
  background-color: var(--mor);
  border-radius: 25px;
}
.power-by {
  padding: 0px 10px;
  background-color: var(--mor);
  border-radius: 25px;
}
.support h2 {
  text-align: center;
}
.power-by h2 {
  text-align: center;
}
.power-by img {
  max-height: var(--f-p-i);
}
.footer-we {
  display: grid;
  grid-template-columns: repeat(2, 50%);
}
.follow-us a img {
  max-height: var(--f-p-i);
}
.footer-we .sign {
  display: flex;
  justify-content: right;
  align-items: flex-end;
  padding: 15px;
}
.shop-name {
  display: flex;
  align-items: center;
  height: 64px;
  overflow: hidden;
}
.shop-name img {
  height: 48px;
  border-radius: 100%;
  padding-left: 15px;
}
.shop-name span {
  padding-left: 15px;
}
.space-s {
  height: 100px;
}
.store {
  height: calc(100% + 30px);
  width: 100%;
  position: relative;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}
.store a {
  position: absolute;
  background-color: white;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  width: 60px;
  border-radius: 10px;
  text-decoration: none;
}
.rm a {
  text-decoration: none;
  display: flex;
  align-items: center;
  justify-content: center;
}
.shop-name span {
  color: var(--toggle-color);
}
.banner {
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: var(--bor-r);
}
.mySlides {
  height: 320px;
  border-radius: 15px;
}
.ads {
  width: 100%;
  border-radius: 15px;
  max-width: calc(100wv * 3);
  position: relative;
  margin: auto;
}
.mySlides .text {
  color: var(--toggle-color);
  padding: 8px 12px;
  position: absolute;
  bottom: 0px;
  width: 100%;
  text-align: center;
  background-color: var(--body-color);
  border-radius: 0 0 15px 15px;
}
.numbertext {
  color: var(--toggle-color);
  padding: 8px 12px;
  position: absolute;
  background-color: var(--body-color);
  border-radius: 15px;
  top: 10px;
  left: 10px;
}
.preload {
  z-index: 99999;
  top: 0;
  width: 100vw;
  height: 100vh;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  overflow: hidden;
  background-color: #000;
}
.f-left {
  z-index: 999999;
  top: 0;
  height: 100vh;
  width: 50vw;
  right: 50vw;
  position: absolute;
  background-color: #000;
  overflow: hidden;
}
.f-left img {
  position: absolute;
  top: calc(50% - 96px);
  right: calc(0vh - 96px);
}
.f-right {
  z-index: 999999;
  top: 0;
  height: 100vh;
  width: 50vw;
  left: 50vw;
  position: absolute;
  background-color: #000;
  overflow: hidden;
}
.f-right img {
  position: absolute;
  top: calc(50% - 96px);
  left: calc(0vh - 96px);
}
.fade {
  animation-name: fade;
  animation-duration: 2s;
}
@keyframes fade {
  from {
    opacity: 0.4;
  }
  to {
    opacity: 1;
  }
}
.fadeout {
  animation-name: fadeout;
  animation-duration: 1s;
}
.t-left {
  animation-name: g-left;
  animation-duration: 1s;
}
.t-right {
  animation-name: g-right;
  animation-duration: 1s;
}
@keyframes g-left {
  0% {
    right: 50vw;
  }
  50% {
    right: 100vw;
    display: none;
  }
  100% {
    right: 100vw;
    display: none;
  }
}
@keyframes g-right {
  0% {
    left: 50vw;
  }
  50% {
    left: 100vw;
    display: none;
  }
  100% {
    left: 100vw;
    display: none;
  }
}
@keyframes fadeout {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 0.9;
    display: none;
  }
  60% {
    opacity: 0.7;
    display: none;
  }
  70% {
    opacity: 0.5;
    display: none;
  }
  80% {
    opacity: 0.3;
    display: none;
  }
  90% {
    opacity: 0.1;
    display: none;
  }
  100% {
    opacity: 0;
    display: none;
  }
}
@media only screen and (max-width: 300px) {
  .mySlides .text {
    font-size: 11px;
  }
}

/* Slideshow */

.slideshow {
  margin: 0 auto;
  overflow: hidden;
  width: 600px;
}

.slideshowSlider {
  white-space: nowrap;
  transition: ease 2000ms;
}

.slide {
  display: inline-block;

  height: var(--goods-h);
  width: var(--goods-w);
}
.context {
  position: absolute;
  bottom: 5px;
  display: flex;
  align-items: center;
  background-color: var(--body-color);
  width: 100%;
  text-align: center;
  color:var(--toggle-color);
  padding: 10px;
}
/* Buttons */

.slideshowDots {
  text-align: center;
}

.slideshowDot {
  display: inline-block;
  height: 20px;
  width: 20px;
  border-radius: 50%;

  cursor: pointer;
  margin: 15px 7px 0px;

  background-color: var(--body-color);
}

.slideshowDot.active {
  background-color: var(--primary-color);
}